<template>
    <div 
        v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%"
        class="post-details">
        <header class="post-head">
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">职位详情</span>
        </header>
        <div class="company-cover-ImgUrl">
            <img :src="coverImgUrl" />
        </div>
        <div class="company-one">
            <div class="company-name">
                <span class="post-type-company">
                    {{companyAlias}}-
                    <span v-if="jobTypeCode3">{{jobTypeCode3}}</span>
                    <span v-else-if="jobTypeCode2">{{jobTypeCode2}}</span>
                </span>
                <el-tag class="recruit-type" v-if="recruitDemandType == 1 || recruitDemandType == null || recruitDemandType == ''">社会工</el-tag>
                <el-tag class="recruit-type" v-if="recruitDemandType == 5">小时工</el-tag>
                <el-tag class="recruit-type" v-if="recruitDemandType == 8">学生工</el-tag>
            </div>
            <p class="post-type">{{jobTypeCode1}}/{{jobTypeCode2}}/{{jobTypeCode3}}</p>
            <p class="post-salary">{{positionLowWages}}-{{positionHighWages}}元/月</p>
            <div class="post-releate-customer">
                <span class="padding-right">关联客户：</span>
                <span>{{customerName}}</span>
            </div>
            <div class="post-releate-customer post-address">
                <span class="padding-right">工作地址：</span>
                <span>{{workProvinceName}}{{workCityName}}{{workDistName}} {{workAddress}}</span>
            </div>
        </div>
        <div class="basic-info">
            <header class="head">
                <i class="head-line"></i>
                <span class="head-font">基本信息</span>
            </header>
            <div class="info-item">
                <span class="padding-right">福利标签</span>
                <span class="item-right" v-for="(item,index) in showChanceWelfareList" :key="index">{{item.name}} </span>
            </div>
            <div class="info-item">
                <span class="padding-right">工作时间</span>
                <span class="item-right">{{positionWorkTime}}</span>
            </div>
            <div class="info-item">
                <span class="padding-right">工作内容</span>
                <span class="item-right">{{positionWorkCont}}</span>
            </div>
            <div class="info-item">
                <span class="padding-right">发薪时间</span>
                <span class="item-right">{{positionPayArrangement}}</span>
            </div>
            <div class="info-item">
                <span class="padding-right">年龄要求</span>
                <span class="item-right">{{positionLowAges}}-{{positionHighAges}}岁</span>
            </div>
            <div class="info-item">
                <span class="padding-right">学历要求</span>
                <span class="item-right">{{positionEducat}}</span>
            </div>
            <div class="info-item">
                <span class="padding-right">性别要求</span>
                <span class="item-right">{{positionSex}}</span>
            </div>
            <div class="info-item">
                <span class="padding-right">咨询电话</span>
                <span class="item-right item-right-no-border">{{positionPhone}}</span>
            </div>
        </div>
        <header class="head job-head">
            <i class="head-line"></i>
            <span class="head-font">投递记录</span>
        </header>
        <div class="deliver" v-if="resumeList.length > 0">
            <div class="deliver-item" v-for="(item,index) in resumeList" :key="index">
                <div class="item-pic">
                    <img class="pic" v-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && item.bornInteger <= 24" :src="resumePicOne">
                    <img class="pic" v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && ((item.bornInteger >= 25 && item.bornInteger <= 34) || item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')" :src="resumePicTwo">
                    <img class="pic" v-else-if="(item.genderString == undefined || item.genderString == null || item.genderString == '') && (item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')" :src="resumePicTwo">
                    <img class="pic" v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (item.bornInteger >= 35 && item.bornInteger <= 44)" :src="resumePicThree">
                    <img class="pic" v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && item.bornInteger >= 45" :src="resumePicFour">
                    <img class="pic" v-else-if="item.genderString == '女' && item.bornInteger <= 24" :src="resumePicFive">
                    <img class="pic" v-else-if="item.genderString == '女' && ((item.bornInteger >= 25 && item.bornInteger <= 34) || item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')" :src="resumePicSix">
                    <img class="pic" v-else-if="item.item.genderString == '女' && (item.bornInteger >= 35 && item.bornInteger <= 44)" :src="resumePicServen">
                    <img class="pic" v-else-if="genderString == '女' && item.bornInteger >= 45" :src="resumePicEight">
                    <img class="gender" v-if="item.genderString == '男'" :src="manIcon">
                    <img class="gender" v-else-if="item.genderString == '女'" :src="womanIcon">
                </div>
                <div class="deliver-item-right">
                    <div class="user-name">
                        <span class="name" v-if="item.name">{{item.name}}</span>
                        <span class="age" v-if="item.bornInteger">{{item.bornInteger}}岁</span>
                    </div>
                    <div class="want-address">
                        <i class="want-icon"></i>
                        <span class="want-name">意向</span>
                        <span class="want-address-font">{{item.intentionalCity[0].intentionalPlace}}</span>
                    </div>
                    <p class="push-time" v-if="item.applyTime">投递时间:{{item.applyTime}}</p>
                </div>
            </div>
            <infinite-loading @infinite="infiniteRecruiting" v-if="listTotalNum > 10">
                <span slot="spinner" class="infinite-load">
                    <i class="el-icon-loading"></i>
                    <!-- <span>亲爱的，您慢点，我加载呢~</span> -->
                </span>
                <span slot="no-more" class="infinite-load">
                    <!-- 数据已全部加载 -->
                </span>
            </infinite-loading>
        </div>
        <div class="list-no-data" v-else-if="resumeListState == true && listTotalNum == 0">
            <img :src="resumeListStateBack" class="bo-data-icon">
            <p class="recruiting-tips">投递记录为空</p>
        </div>
        <div class="release-foot" v-if="postHeadTab == 0">
            <el-button type="primary" class="release-btn release-save" @click="postLower">下架</el-button>
            <el-button type="primary" class="release-btn release-save-release" @click="postRefresh">刷新</el-button>
        </div>
        <div class="release-foot" v-else-if="postHeadTab == 1 || postHeadTab == 3">
            <el-button type="primary" class="release-btn release-save" @click="postEdit">编辑</el-button>
            <el-button type="primary" class="release-btn release-save-release" @click="postUpper">上架</el-button>
        </div>
        <!-- popup的mask -->
        <div class="mask" v-show="refreshPopup"></div>
        <!--刷新model -->
        <div class="score_modal" v-if="closeRefreshShelfTipLstorage == 'false'" v-show="refreshPopup">
            <p class="f-28 c-12221F mb-2 lh-1 mb-10">刷新职位，将花费您10积分</p>
            <div class="refresh-check">
                <el-checkbox v-model="refreshShelfChecked" class="el-checkboxno">我知道了，下次不再提示</el-checkbox>
            </div>
            <div class="modal_footerBtn">
                <div class="btn" @click="hideRefreshPopup">取消</div>
                <div class="btn" @click="refreshSure">确定</div>
            </div>
        </div>
    </div>
</template>

<script>
import Util from '@/js/mUtils';
import {DLDict,DLArea,DLPType,DLIndustry} from '@/js/dl-code';
import { MessageBox  } from 'mint-ui';
import { Notify  } from 'vant';
import InfiniteLoading from 'vue-infinite-loading';                     //分页加载组件
import resumeListStateBack from "@/assets/post/resume_list_state_back.png";
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
import manIcon from "@/assets/post/man_icon.png";
import womanIcon from "@/assets/post/woman_icon.png";
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 全局域名
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loading: false,
            resumePicOne: resumePicOne ,
            resumePicTwo: resumePicTwo ,
            resumePicThree: resumePicThree ,
            resumePicFour: resumePicFour,
            resumePicFive: resumePicFive ,
            resumePicSix: resumePicSix ,
            resumePicServen: resumePicServen,
            resumePicEight: resumePicEight,
            manIcon: manIcon,
            womanIcon: womanIcon,
            postHeadTab: '',
            // 职位详情开始
            customerName: '',
            coverImgUrl: '',
            companyAlias: '',                       //企业简称
            jobTypeCode1: '',
            jobTypeCode2: '',                       //岗位类型2
            jobTypeCode3: '',                        //岗位类型3
            recruitDemandType: '',                   //招聘类型
            workProvinceName: '',
            workCityName: '',
            workDistName: '',
            workAddress: '',
            positionLowWages: '',
            positionHighWages: '',
            positionLowAges: '',
            positionHighAges: '',
            positionWorkCont: '',
            positionPayArrangement: '',
            positionWorkTime: '',
            positionPhone: '',
            // 学历
            positionEducatVal:'',
            positionEducat: '',
            educationList: [],
            // 性别
            positionSexVal: '',
            positionSex: '',
            sexList: [],
            showChanceWelfareList: [],               //福利标签
            // 投递记录
            genderString: '',                     //性别
            bornInteger: '',                        //年龄
            page: 1,
            resumeListState: false,             //投递记录没有数据
            resumeListStateBack: resumeListStateBack,   //投递记录没有数据
            listPageState: false,               //是否有分页
            listTotalNum: '',                   //投递记录列表长度
            resumeList: [],                     //投递记录
            refreshPopup: false,            //刷新弹窗
            refreshShelfChecked: '',            // 刷新不在提示复选框状态值
            closeRefreshShelfTipLstorage: '',   //在一个浏览器中不清除localStorage永远关闭刷新弹窗
            qdsUserId: '',              //渠道商id
            positionId: '',                     //职位id
        }
    },
    components: {
        Notify,
        MessageBox,
        InfiniteLoading,        //分页
    },
    mounted() {
        this.qdsUserId = localStorage.getItem('qdsSCorpUserId')
        this.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + this.qdsUserId)                         //刷新弹窗-是否勾选了不在提示状态
        if(this.closeRefreshShelfTipLstorage == null || this.closeRefreshShelfTipLstorage == undefined) {
            this.closeRefreshShelfTipLstorage = 'false'
        }
        this.positionId = this.$route.query.positionId; 
        this.companyAlias = this.$route.query.companyAlias;
        this.postHeadTab = this.$route.query.postHeadTab;
        this.educationList = DLDict.educationRequire;
        this.sexList = DLDict.genderRequire;
        this.getPositionDetails();               //职位管理-编辑职位-职位详情
        this.getResumeList();                    //投递记录
    },
    methods: {
        backHistory() {
            window.history.back(-1)
        },
        getPositionDetails() {                          //职位管理-编辑职位-职位详情
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.axios(that.qdsGlobalApiUrl+ 'channelDemand/getById?token=' + token,{
                method: 'post',
                params: {
                    corpDemandId: that.positionId
                }
            })
            .then(function (response) {  
                if (response.data.code == 10000) {
                    if(response.data.data.coverImgUrl) {
                        that.coverImgUrl = response.data.data.coverImgUrl
                    }
                    if(response.data.data.workProvinceCode) {
                        that.workProvinceName = DLArea.getName(response.data.data.workProvinceCode)
                    }
                    if(response.data.data.workCityCode) {
                        that.workCityName = DLArea.getName(response.data.data.workCityCode)
                    }
                    if(response.data.data.workDistCode) {
                        that.workDistName = DLArea.getName(response.data.data.workDistCode)
                    }
                    if(response.data.data.address) {
                        that.workAddress = response.data.data.address
                    }
                    if(response.data.data.jobTypeCode3) {
                        that.jobTypeCode3 = DLPType.getName(response.data.data.jobTypeCode3)
                    }
                    if(response.data.data.jobTypeCode2) {
                        that.jobTypeCode2 = DLPType.getName(response.data.data.jobTypeCode2)
                    }
                    if(response.data.data.jobTypeCode1) {
                        that.jobTypeCode1 = DLPType.getName(response.data.data.jobTypeCode1)
                    }
                    if(response.data.data.recruitDemandType) {
                        that.recruitDemandType = response.data.data.recruitDemandType
                    }
                    if(response.data.data.salaryFrom || response.data.data.salaryTo) {
                        that.positionLowWages = response.data.data.salaryFrom        //结算标准页面显示综合薪资元/月低
                        that.positionHighWages = response.data.data.salaryTo     
                    }
                    if(response.data.data.corpName) {
                        that.customerName = response.data.data.corpName
                    }
                    if(response.data.data.ageFrom || response.data.data.ageTo) {
                        that.positionLowAges = response.data.data.ageFrom        
                        that.positionHighAges = response.data.data.ageTo     
                    }
                    if(response.data.data.jobContent) {
                        that.positionWorkCont = response.data.data.jobContent            //工作内容
                    }
                    if(response.data.data.payArrangement) {
                        that.positionPayArrangement = response.data.data.payArrangement     //发薪时间
                    }
                    if(response.data.data.workTime) {
                        that.positionWorkTime = response.data.data.workTime                 //工作时间
                    }
                    if(response.data.data.consultTel) {
                        that.positionPhone = response.data.data.consultTel                 //咨询电话
                    }
                    if(response.data.data.educationMin) {
                        that.positionEducatVal = Number(response.data.data.educationMin)                //学历要求
                        that.positionEducat = that.educationList[that.positionEducatVal]
                    }
                    that.positionSexVal = Number(response.data.data.gender)                         //性别要求
                    that.positionSex = that.sexList[that.positionSexVal]
                    
                    
                    that.showChanceWelfareList = response.data.data.welfareLabel  
                }else {
                    // that.$message({
                    //     message: response.data.message + '，请联系客服021-22876396'   + '(错误码: ' + response.data.code + ')',
                    //     type: 'error',
                    //     duration: 2000
                    // })
                }
            })
            .catch(function (error) {        
            });
        },
        getResumeList() {
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.page = 1
            that.axios(that.qdsGlobalApiUrl+ 'channelResume/getResumeListByDemandId?token=' + token,{
                method: 'get',
                params: {
                    page: that.page,
                    limit: 10,
                    demandId: that.positionId
                }
            })
            .then(response => {
                if(response.data.code == 10000) {
                    that.resumeList = response.data.data
                    that.listTotalNum = that.resumeList.length
                    if(that.listTotalNum == 0) {
                        that.resumeListState = true;
                    }else if(that.listTotalNum > 10) {
                        that.listPageState = true
                    }else if(that.listTotalNum <= 10){
                        that.listPageState = false
                    }
                }
            })
            .catch(error => {

            })
        },
        infiniteRecruiting($state) {                                            //投递记录分页
            let token = Util.getCookie('qdsMToken');
            let that= this;
            if(that.listPageState) {
                that.page = that.page + 1;
                that.axios(that.qdsGlobalApiUrl+ 'channelResume/getResumeListByDemandId?token=' + token,{
                    method: 'get',
                    params: {
                        page: that.page,
                        limit: 10,
                        demandId: that.positionId
                    }
                })
                .then(response=>{
                    if(response.data.code == 10000) {
                        if (response.data.data) {
                            if(response.data.data.length < 10) {
                                that.listPageState = false
                            }else {
                                that.listPageState = true
                            }
                            that.resumeList = that.resumeList.concat(response.data.data);
                            $state.loaded();
                        }else {
                            $state.complete();
                        }
                    }else {
                        Notify({
                            message: '列表分页数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    }
                })
                .catch( error=> {
                });
            }else {
                $state.complete();
            }
        },
        postUpper(postID) {               //职位上架
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.loading = true;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateStatusNoById?token=' + token,{
                method: 'post',
                params: {
                    demandId: that.positionId,
                    operationType: 1,
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    that.loading = false;
                    Notify({
                        message: '职位上架成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    that.$router.push('/qdsPostList?postHeadTab=' + 2)
                }else {
                    that.loading = false;
                    Notify({
                        message: '职位上架失败，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
                that.loading = false;
                Notify({
                    message: '职位上架失败，请联系客服021-22876396',
                    duration: 2000,
                    background: '#FF6262'
                });
            });
        },
        hideRefreshPopup() {                    //隐藏刷新弹窗
            this.refreshPopup = false
        },
        refreshSure() {                  //职位刷新-显示弹窗（没有点击不在提示的刷新按钮）
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.loading = true;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/refreshById?token=' + token,{
                method: 'post',
                params: {
                    id: that.positionId,
                },
                timeout: 10000
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    that.loading = false;
                    that.refreshPopup = false;
                    if(that.refreshShelfChecked == true) {  
                        localStorage.setItem('closeRefreshShelfTipLstorage'+ that.qdsUserId,'true')
                        that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                        that.refreshPopup = false
                    }else{
                        that.refreshPopup = false
                    }
                    Notify({
                        message: '职位刷新成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    if(that.postHeadTab == 0) {
                        that.getRecruitingList()            //获取正在招聘列表
                    }else if(that.postHeadTab == 2) {
                        that.getInAuditList()               //获取审核中列表
                    }
                }else {
                    that.loading = false;
                    that.refreshPopup = false;
                    if(that.refreshShelfChecked == true) {  
                        localStorage.setItem('closeRefreshShelfTipLstorage'+that.qdsUserId,'true')
                        that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                        that.refreshPopup = false
                    }else{
                        that.refreshPopup = false
                    }
                    Notify({
                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
                that.loading = false;
                that.refreshPopup = false;
                if(that.refreshShelfChecked == true) {  
                    localStorage.setItem('closeRefreshShelfTipLstorage'+that.qdsUserId,'true')
                    that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                    that.refreshPopup = false
                }else{
                    that.refreshPopup = false
                }
                Notify({
                    message: '职位刷新失败，请联系客服021-22876396',
                    duration: 2000,
                    background: '#FF6262'
                });
            });
        },
        postRefresh() {                         //职位刷新弹窗确认-根据closeRefreshShelfTipLstorage状态是否显示隐藏刷新弹窗
            var that = this;
            var token = Util.getCookie('qdsMToken');
            if(that.closeRefreshShelfTipLstorage == 'false') {
                that.refreshPopup = true;
            }else if(that.closeRefreshShelfTipLstorage == 'true') {
                that.loading = true;
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/refreshById?token=' + token,{
                    method: 'post',
                    params: {
                        id: that.positionId,
                    },
                    timeout: 10000
                })
                .then(response=>{
                    if(response.data.code == 10000) {
                        that.loading = false;
                        Notify({
                            message: '职位刷新成功',
                            duration: 2000,
                            background: '#20D3B3'
                        });
                    }else {
                        that.loading = false;
                        Notify({
                            message: response.data.message + '(错误码: ' + response.data.code + ')',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    }
                })
                .catch( error=> {
                    that.loading = false;
                    Notify({
                        message: '职位刷新失败，请联系客服021-22876396',
                        duration: 2000,
                        background: '#FF6262'
                    });
                });
            }
        },
        postLower() {               //下架弹窗
            var that= this;
            MessageBox({
                title: '提示',
                message: '确定下架该职位?',
                showCancelButton: true
            })
            .then(action => { 
                if (action == 'confirm') {      //确认的回调
                    that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateStatusNoById?token=' + token,{
                        method: 'post',
                        params: {
                            demandId: that.positionId,
                            operationType: 2,
                        }
                    })
                    .then(response=>{
                        if(response.data.code == 10000) {
                            that.loading = false;
                            that.$router.push('/qdsPostList?postHeadTab=' + 3)
                            Notify({
                                message: '职位下架成功',
                                duration: 2000,
                                background: '#20D3B3'
                            });
                            
                        }else {
                            that.loading = false;
                            Notify({
                                message: '职位下架失败，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                                duration: 2000,
                                background: '#FF6262'
                            });
                        }
                    })
                    .catch( error=> {
                        that.loading = false;
                        Notify({
                            message: '职位下架失败，请联系客服021-22876396',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    });
                }
             })
            .catch(err => { 
                 if (err == 'cancel') {         //取消的回调
                 } 
            });
        },
        postEdit() {                //编辑-跳转到发布职位页面
            this.$router.push('/qdsReleaseNewPost?positionId=' + this.positionId)
        }
    }
}
</script>

